/// @group Colors
$c-link: #EA3223;
$c-red: #ff0000;
$c-black: #1A272E;
$c-gr7d: #7d7d7d;

// Z-indices
/// @group Z-Index
/// @type z-index
$z-core: -100;

/// @group Z-Index
/// @type z-index
$z-crust: 0;

/// @group Z-Index
/// @type z-index
$z-atmosphere: 100;

/// @group Z-Index
/// @type z-index
$z-stratosphere: 200;

/// @group Z-Index
