$ff-sans: "Barlow Condensed", Helvetica, sans-serif;
$ff-din: $ff-sans;

$ff-helv: Helvetica, sans-serif;

/// Font-Weights
/// @group Font Weight
$fw-thin: 100;

/// @group Font Weight
$fw-light: 300;

/// @group Font Weight
